	module.exports = {"data":{"wordpressPost":{"title":"I Wore Gel Nail Polish For FIVE Years And This Happened &#8230;","content":"<p>Remember when gel nails first arrived? I sure do. I was a Beauty Editor at The Sunday Telegraph at the time and a new gel nail company offered to come by my office and apply this amazing-sounding new nail product right at my desk.</p>\n<p>So I said YES PLEASE. Because well, who wouldn&#8217;t?</p>\n<p>The fabulous new nail lacquer was applied in 20 minutes or so. Unlike regular polish, it was cured or set in a cool little light box and voila &#8211; my fingernails were gorgeous with rock-hard, seriously shiny nails that did not chip or break for three glorious weeks.</p>\n<p>I was so impressed I wore this mind-boggling new gel nail product for the next five years. Yes. <strong>FIVE years.</strong></p>\n<p>Of course, as anyone who has ever used gel nails will know, my fingernails always looked amazing. I had that just walked out of the salon look only ALL the time! At least, I did until the day I decided to quit using them and discovered that the health of my natural nails was now somewhat dubious.</p>\n<p>The colour on my nails were now distinctly yellow and looked as though I&#8217;d smoked a carton of cigarettes every day since birth. The nails themselves were thin and fragile too. Bummer.</p>\n<img class=\"alignnone size-medium wp-image-3222\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/23200832/Delicate-Collection-300x212.jpg\" alt=\"\" width=\"300\" height=\"212\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/23200832/Delicate-Collection-300x212.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/23200832/Delicate-Collection-768x543.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/23200832/Delicate-Collection-1024x724.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><a href=\"https://www.mavala.com.au/color/#block-yui_3_17_2_69_1482530320314_56943\">Mavala Delicate Collection, $8.50.</a></p>\n<p>I knew that something had to give and so I went to <strong>Tracey Winder, manicurist and Mavala National Trainer</strong>, and begged for help. Firstly, she confirmed my worst fears. Gel nails worn long-term are not the best. Sigh.</p>\n<p>&#8220;Wearing gels for such an intensive period without a break is harmful to your nails in several ways,&#8221; said Tracey.</p>\n<p>&#8220;Some nail polishes, in particular gel ones, are non porous. They occlude the nail, suffocating it from airflow and resulting in a weaker, softer nail. This weakening of the nail surface has a knock-on effect on the nail bed and root, which sees critical blood and nutrients flow reduced. This impacts nail growth and can result in a poorly formed nail plate.&#8221;</p>\n<p>Super bummer.</p>\n<p>And it gets worse. Apparently, removing gel nails with acetone creates a further myriad of issues from dryness through to peeling and brittleness.</p>\n<p>That is why it is important to detox between cycles of gel nails and allow the nail time to recover, said Tracey.</p>\n<p>Whilst in recovery mode, it’s all about treatment and feeding the nails with topically applied products and a diet rich in vitamins and minerals.</p>\n<p>Apparently, a key mineral for nails is silicium, which is literally like ‘cell cement’ and helps nails to grow faster and more resistant.</p>\n<img class=\"alignnone size-medium wp-image-3223\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/23200952/9090561F-215x300.jpg\" alt=\"\" width=\"215\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/23200952/9090561F-215x300.jpg 215w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/23200952/9090561F-768x1069.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/23200952/9090561F-735x1024.jpg 735w\" sizes=\"(max-width: 215px) 100vw, 215px\" />\n<p><a href=\"https://www.mavala.com.au/new-products/nailactan-1\">Mavala Nailactan Nail Cream, $31.95.</a></p>\n<p>&#8220;I always advise clients to use an intensive nail treatment cream or serum, rich in high performing ingredients that will combat dryness and nourish the nail,&#8221; added Tracey, who likes <strong>Mavala&#8217;s Nailactan Nail Cream.</strong></p>\n<p>&#8220;It&#8217;s crammed with essential amino acids, lipids and vitamins. It nourishes the nail, counteracts dryness and helps to restore elasticity to the nail plate, thus strengthening nails.&#8221;</p>\n<p>Her final piece of advice to me and my sad nails. Show them some love.</p>\n<p>&#8220;Your nails will need a lot of TLC in the detox and recovery period to restore natural nail health,&#8221; said Tracey.</p>\n<p>&#8220;The average cycle of nail growth is three to five months, so I would advise infrequent application of gels to maintain optimum nail health year round.&#8221;</p>\n<p>And when they recover I can tell you exactly what I&#8217;ll do &#8230; get gel nails again. Because let&#8217;s face it &#8211; they are ACE! Only this time, I will give my nails a break from time to time. And that way, Tracey won&#8217;t have to provide all this advice all over again.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/23201726/JPG-Mav-Blue.jpg"},"categories":[{"slug":"lifestyle","name":"Lifestyle"},{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"c9fcd182-078d-58bf-9d4f-83d5aae2ce50"}}

